<template>
  <div class="page trialpreson flex-v trialdoctor">
    <div class="headertitle flex">
      <el-breadcrumb separator-class="el-icon-arrow-right"
        class="my-breadcrumb ">
        <el-breadcrumb-item :to="{name:'trialdoctor'}">初审医生工作量</el-breadcrumb-item>
        <el-breadcrumb-item>更多</el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!-- 初审医生工作量 -->
    <div class="table-box flex-item"
      style="padding-top:20px">
      <el-table stripe
        ref="multipleTable"
        :data="tableData"
        tooltip-effect="dark"
        height='100%'
        v-loading="loading"
        header-cell-class-name='custom-table-cell'>
        <el-table-column type="index"
          width="50"
          label="序号">
          <template v-slot='scope'>
            {{scope.$index + (pageNum - 1) * pageSize + 1}}
          </template>
        </el-table-column>
        <el-table-column align="center"
          prop="userName"
          label="姓名">
        </el-table-column>
        <el-table-column align="center"
          label="性别">
          <template slot-scope="scope">
            <div v-if="scope.row.sexType==1">男</div>
            <div v-else-if="scope.row.sexType==2">女</div>
            <div v-else>不详</div>
          </template>
        </el-table-column>
        <el-table-column align="center"
          label="年龄">
          <template slot-scope="scope">{{scope.row.age | emptyData}}</template>
        </el-table-column>
        <el-table-column align="center"
          label="联系电话">
          <template slot-scope="scope">{{scope.row.contactPhone | emptyData}}</template>
        </el-table-column>

      </el-table>
    </div>

    <!-- 分页 -->
    <div class="footer">
      <bj-pagination :query-obj="$route.query"
        query-url="/charts/base/findDoctorCheckPerson"
        @listResult="getListResult"
        ref="paginationEvent"
        @controlLoading="loading=$event" />
    </div>
  </div>
</template>
<script>
export default {
  name: 'trialdoctormore',
  data() {
    return {
      tableData: [],
      pageNum: 1,
      pageSize: 10,
      loading: false
    }
  },

  methods: {
    //获取数据
    getListResult({ list, pageNum, pageSize }) {
      this.tableData = list
      this.pageNum = pageNum
      this.pageSize = pageSize
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../styles/FirstTrial/fristtrial.less';
.trialdoctor {
  .padding20 {
    padding: 20px;
  }
  .headertitle {
    align-items: center;
    justify-content: space-between;
  }
  .rightbtn {
    display: flex;
    .marginright50 {
      margin-right: 50px;
    }
    div {
      cursor: pointer;
      display: flex;
      align-items: center;
      font-size: 14px;
      color: #666666;
      span {
        width: 25px;
        height: 60px;
      }
    }
  }
  .countlist {
    margin-bottom: 20px;
    .countlist-item {
      width: 300px;
      height: 118px;
      box-shadow: 0px 3px 10px 0px rgba(12, 52, 104, 0.15);
      border-radius: 6px;
      justify-content: center;
      flex-direction: column;
      padding: 0 30px;
      margin-right: 20px;
      .itemtitle {
        font-size: 14px;
      }
      .itemcount {
        font-size: 36px;
        font-weight: 700;
      }
    }
    .countlist-item:last-child {
      margin-right: 20px;
    }
  }
}
</style>

